
'use client'; // Convert HomePage to a Client Component

import { useEffect, useState } from 'react';
import type { Competition, User, Building } from '@/types'; // Added Building type
import { getCompetitions, getActiveWorks, fetchUserSessionForClient } from '@/app/actions'; // Added getActiveWorks
import BuildingCard from '@/components/minecraft/BuildingCard';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { ServerCrash } from 'lucide-react'; // For error display

export default function HomePage() {
  const [competitions, setCompetitions] = useState<Competition[]>([]);
  const [activeWorks, setActiveWorks] = useState<Building[]>([]);
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        setError(null);
        // Fetch data in parallel
        const [compsData, worksData, userData] = await Promise.all([
          getCompetitions().catch(e => { console.error("Failed to fetch competitions", e); return []; }),
          getActiveWorks().catch(e => { console.error("Failed to fetch active works", e); return []; }),
          fetchUserSessionForClient().catch(e => { console.error("Failed to fetch user session", e); return null; })
        ]);
        setCompetitions(compsData);
        setActiveWorks(worksData);
        setCurrentUser(userData);
      } catch (err) {
        console.error("Error fetching homepage data:", err);
        setError("无法加载活动数据，请稍后再试。");
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="space-y-6">
        <div className="h-8 bg-muted rounded w-1/4 animate-pulse mb-2"></div> {/* Title Skeleton */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {[...Array(4)].map((_, i) => (
            <div key={i} className="bg-card border-border rounded-lg shadow-lg flex flex-col overflow-hidden">
              <div className="w-full aspect-[16/9] bg-muted animate-pulse"></div>
              <div className="p-4 flex flex-col flex-grow">
                <div className="h-5 bg-muted rounded w-3/4 mb-2 animate-pulse"></div>
                <div className="h-3 bg-muted rounded w-1/2 mb-1 animate-pulse"></div>
                <div className="h-3 bg-muted rounded w-full mb-3 animate-pulse"></div>
                <div className="h-3 bg-muted rounded w-2/3 mb-4 animate-pulse"></div>
                <div className="mt-auto flex justify-between items-center">
                    <div className="flex gap-3">
                        <div className="h-4 bg-muted rounded w-12 animate-pulse"></div>
                        <div className="h-4 bg-muted rounded w-10 animate-pulse"></div>
                    </div>
                    <div className="h-8 bg-muted rounded w-24 animate-pulse"></div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive" className="mt-6">
        <ServerCrash className="h-5 w-5" />
        <AlertTitle>数据加载错误</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  const displayActiveWorks = activeWorks && activeWorks.length > 0;

  return (
    <div className="space-y-6">
      <h2 className="text-2xl font-semibold text-foreground tracking-tight">
        {displayActiveWorks ? "热门进行中作品" : "所有活动"}
      </h2>
      
      {displayActiveWorks ? (
        activeWorks.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
            {activeWorks.map((building) => (
              <BuildingCard 
                key={building.id} 
                building={building} 
                currentUser={currentUser} 
              />
            ))}
          </div>
        ) : (
          <p className="text-center text-muted-foreground text-lg py-10">
            当前暂无进行中的作品。
          </p>
        )
      ) : (
        competitions.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
            {competitions.map((competition) => (
              <BuildingCard 
                key={competition.id} 
                building={{ 
                  id: competition.id, 
                  name: competition.name, 
                  description: competition.description || '暂无描述',
                  imageUrl: `https://placehold.co/600x338.png?text=${encodeURIComponent(competition.name)}`,
                  aiHint: `event ${competition.name.substring(0, 15)}`, // Make aiHint shorter
                  status: '有效', 
                  author: '活动主办方',
                  votes: 0, 
                  size: '中型', 
                  territoryIdentifier: '',
                  server: '',
                  coordinates: {x:0,y:0,z:0},
                  eventEdition: competition.year,
                  eventYear: competition.year,
                  competitionId: competition.id,
                }} 
                competition={competition}
                currentUser={currentUser} 
              />
            ))}
          </div>
        ) : (
          <p className="text-center text-muted-foreground text-lg py-10">
            当前暂无活动。敬请期待！
          </p>
        )
      )}
    </div>
  );
}
